import React from 'react';
import { Link } from 'react-router-dom';

const Header = ({ username }) => (
  <header className="top-area">
    <div className="header-area">
      <nav className="navbar navbar-default bootsnav navbar-fixed dark no-background">
        <div className="container">
          <div className="navbar-header">
            <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#navbar-menu">
              <i className="fa fa-bars"></i>
            </button>
            <Link className="navbar-brand" to="/">경성대학교</Link>
          </div>
          <div className="collapse navbar-collapse menu-ui-design" id="navbar-menu">
            <ul className="nav navbar-nav navbar-right" data-in="fadeInDown" data-out="fadeOutUp">
              <li className="smooth-menu active"><Link to="/">Home</Link></li>
              <li className="smooth-menu"><Link to="/education">비전체계도</Link></li>
              <li className="smooth-menu"><Link to="/skills">대학</Link></li>
              <li className="smooth-menu"><Link to="/experience">학사정보</Link></li>
              <li className="smooth-menu"><Link to="/profiles">SNS</Link></li>
              <li className="smooth-menu"><Link to="/portfolio">캠퍼스소개</Link></li>
              <li className="smooth-menu"><Link to="/clients">바로가기</Link></li>
              <li className="smooth-menu">
                {username ? (
                  <span>{username}</span>
                ) : (
                  <Link to="/login">로그인</Link>
                )}
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  </header>
);

export default Header;