import React from 'react';

const Education = () => (
  <section id="education" className="education">
    <div className="section-heading text-center">
      <h2>비전체계도</h2>
    </div>
    <div className="container">
      <div className="education-horizontal-timeline">
        <div className="row">
          <div className="col-sm-4">
            <div className="single-horizontal-timeline">
              <div className="experience-time">
                <h2>비전 2020+</h2>
                <h3>진리를 향한 교육, 내일을 여는 경성</h3>
              </div>
              <div className="timeline-horizontal-border">
                <i className="fa fa-circle" aria-hidden="true"></i>
                <span className="single-timeline-horizontal"></span>
              </div>
              <div className="timeline">
                <div className="timeline-content">
                  <h4 className="title">슬로건</h4>
                  <h5>꿈은 나로부터, 미래는 경성으로부터</h5>
                </div>
              </div>
            </div>
          </div>
          <div className="col-sm-4">
            <div className="single-horizontal-timeline">
              <div className="experience-time">
                <h2>전략 방향</h2>
                <h3>최고를 향해 나아가다.</h3>
              </div>
              <div className="timeline-horizontal-border">
                <i className="fa fa-circle" aria-hidden="true"></i>
                <span className="single-timeline-horizontal"></span>
              </div>
              <div className="timeline">
                <div className="timeline-content">
                  <h4 className="title">취창업, 지역사회, 국제화, 교육</h4>
                  <h5>creativity / connection / readyism</h5>
                </div>
              </div>
            </div>
          </div>
          <div className="col-sm-4">
            <div className="single-horizontal-timeline">
              <div className="experience-time">
                <h2>발전 전략</h2>
                <h3>교육, 산학협력, 국제화, 취창업, 운영 및 인프라</h3>
              </div>
              <div className="timeline-horizontal-border">
                <i className="fa fa-circle" aria-hidden="true"></i>
                <span className="single-timeline-horizontal spacial-horizontal-line"></span>
              </div>
              <div className="timeline">
                <div className="timeline-content">
                  <h4 className="title">발전 전략</h4>
                  <p className="description">KS신교육 모델 기반의 교육과정 고도화 / 지역사회 연계 기반의 산학협력 및 연구, 교육 강화 / 다양성 및 내실화를 통한 국제화 역량 강화 / 학생, 사회 수요 맞춤형 취창업 교육체계 및 지원방안 고도화 / 미래사회를 위한 스마트 캠퍼스 구축 인프라 및 시스템 지원</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
);

export default Education;