import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

const Register = ({ handleRegister }) => {
  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  const [success, setSuccess] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    handleRegister(name, password);
    setSuccess(true);
  };

  const handleConfirm = () => {
    setSuccess(false);
    navigate('/login');
  };

  return (
    <section id="register" className="register">
      <div className="section-heading text-center">
        <h2>회원 가입</h2>
      </div>
      <div className="container">
        <div className="register-content">
          {success ? (
            <div className="alert alert-success" role="alert">
              회원가입이 되었습니다.
              <button onClick={handleConfirm} className="btn btn-primary">확인</button>
            </div>
          ) : (
            <div className="row">
              <div className="col-md-offset-1 col-md-5 col-sm-6">
                <div className="single-register-box">
                  <div className="register-form">
                    <form onSubmit={handleSubmit}>
                      <div className="row">
                        <div className="col-sm-6 col-xs-12">
                          <div className="form-group">
                            <input
                              type="text"
                              className="form-control"
                              id="name"
                              placeholder="Name*"
                              name="name"
                              value={name}
                              onChange={(e) => setName(e.target.value)}
                              required
                            />
                          </div>
                        </div>
                        <div className="col-sm-6 col-xs-12">
                          <div className="form-group">
                            <input
                              type="password"
                              className="form-control"
                              id="password"
                              placeholder="Password*"
                              name="password"
                              value={password}
                              onChange={(e) => setPassword(e.target.value)}
                              required
                            />
                          </div>
                        </div>
                      </div>
                      <div className="row">
                        <div className="col-sm-12">
                          <div className="single-register-btn">
                            <button type="submit" className="register-btn">회원가입</button>
                          </div>
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
              <div className="col-md-offset-1 col-md-5 col-sm-6">
                <div className="single-register-box">
                  <div className="register-adress">
                    <div className="register-add-head">
                      <h3>경성대학교</h3>
                      <p>회원가입</p>
                    </div>
                  </div>
                  <div className="hm-foot-icon">
                    <ul>
                      <li><a href="https://www.facebook.com/KyungsungUni/?locale=ko_KR"><i className="fa fa-facebook"></i></a></li>
                      <li><a href="https://www.instagram.com/ks.ac.kr/"><i className="fa fa-instagram"></i></a></li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </section>
  );
};

export default Register;