import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

const Login = ({ handleLogin }) => {
  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    if (handleLogin(name, password)) {
      navigate('/');
    } else {
      setError('아이디 또는 비밀번호가 잘못되었습니다.');
    }
  };

  return (
    <section id="login" className="login">
      <div className="section-heading text-center">
        <h2>로그인</h2>
      </div>
      <div className="container">
        <div className="login-content">
          <div className="row">
            <div className="col-md-offset-1 col-md-5 col-sm-6">
              <div className="single-login-box">
                <div className="login-form">
                  <form onSubmit={handleSubmit}>
                    <div className="row">
                      <div className="col-sm-6 col-xs-12">
                        <div className="form-group">
                          <input
                            type="text"
                            className="form-control"
                            id="name"
                            placeholder="Name*"
                            name="name"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            required
                          />
                        </div>
                      </div>
                      <div className="col-sm-6 col-xs-12">
                        <div className="form-group">
                          <input
                            type="password"
                            className="form-control"
                            id="password"
                            placeholder="Password*"
                            name="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            required
                          />
                        </div>
                      </div>
                    </div>
                    {error && <p style={{ color: 'red' }}>{error}</p>}
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="single-login-btn">
                          <button type="submit" className="login-btn">로그인</button>
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
            <div className="col-md-offset-1 col-md-5 col-sm-6">
              <div className="single-login-box">
                <div className="login-adress">
                  <div className="login-add-head">
                    <h3>경성대학교</h3>
                    <p>로그인</p>
                  </div>
                </div>
                <div className="hm-foot-icon">
                  <ul>
                    <li><a href="https://www.facebook.com/KyungsungUni/?locale=ko_KR"><i className="fa fa-facebook"></i></a></li>
                    <li><a href="https://www.instagram.com/ks.ac.kr/"><i className="fa fa-instagram"></i></a></li>
                  </ul>
                </div>
                <div className="register-link">
                  <p>계정이 없으신가요? <Link to="/register">회원가입</Link></p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Login;