import React from 'react';

const Portfolio = () => (
  <section id="portfolio" className="portfolio">
    <div className="portfolio-details">
      <div className="section-heading text-center">
        <h2>경성대학교로.</h2>
      </div>
      <div className="container">
        <div className="portfolio-content">
          <div className="isotope">
            <div className="row">
              <div className="col-sm-4">
                <div className="item">
                  <img src="assets/images/map2.jpg" alt="portfolio image"/>
                  <div className="isotope-overlay">
                    <a href="https://kscms.ks.ac.kr/kor/CMS/Contents/Contents.do?mCode=MN042">
                      오시는 길
                    </a>
                  </div>
                </div>							
              </div>
              <div className="col-sm-4">
                <div className="item">
                  <img src="assets/images/map.jpg" alt="portfolio image"/>
                  <div className="isotope-overlay">
                    <a href="https://kscms.ks.ac.kr/kor/CMS/CampusMgr/list.do?mCode=MN041">
                      캠퍼스 지도
                    </a>
                  </div>
                </div>								
              </div>
              <div className="col-sm-4">
                <div className="item">
                  <img src="assets/images/경성이.jpg" alt="portfolio image"/>
                  <div className="isotope-overlay">
                    <a href="https://kscms.ks.ac.kr/kor/CMS/Contents/Contents.do?mCode=MN193">
                      캐릭터
                    </a>
                  </div>
                </div>							
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
);

export default Portfolio;